import {
  Button,
  Tag,
  Table,
  Modal,
  Form,
  Input,
  DatePicker,
  Popconfirm,
  message,
} from "antd";
import React, { useState, useEffect } from "react";
import { getTaskList, addTask, removeTask, completeTask } from "@/api";
import "./index.less";
import { flushSync } from "react-dom";

// 解构textrea
const { TextArea } = Input;

// 日期处理 yyyy-MM-dd HH:mm:ss -> MM-dd HH:mm:ss
const zero = function (text) {
  text = String(text);
  return text.length < 2 ? "0" + text : text;
};
const formatTime = function (time) {
  let arr = time.match(/\d+/g),
    [, month, day, hours = "00", minutes = "00"] = arr;
  return `${zero(month)}-${zero(day)} ${zero(hours)}:${zero(minutes)}`;
};

const TaskTestOfFun = function () {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [tableLoading, setTableLoading] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0); // 完成状态
  const [tableData, setTableData] = useState([]);

  // let formRef = useRef(null);
  const [formIns] = Form.useForm();

  const columns = [
    {
      title: "编号",
      dataIndex: "id",
      align: "center",
      width: "8%",
    },
    {
      title: "任务描述",
      dataIndex: "task",
      ellipsis: true,
      width: "50%",
    },
    {
      title: "状态",
      dataIndex: "state",
      render: (text) => (text === 2 ? "已完成" : "未完成"),
      align: "center",
    },
    {
      title: "完成时间",
      dataIndex: "time",
      align: "center",
      render: (_, record) => {
        let { state, time, complete } = record;
        if (+state === 2) time = complete;

        return formatTime(time);
      },
    },
    {
      title: "操作",
      key: "action",
      render: (_, record) => {
        const { state, id } = record;
        return (
          <>
            <Popconfirm
              title="您确认要删除此任务?"
              onConfirm={handleDel.bind(null, id)}
              okText="确定"
              cancelText="取消"
            >
              <Button type="link">删除</Button>
            </Popconfirm>

            {state === 1 ? (
              <Button type="link" onClick={completeHandle.bind(null, id)}>
                完成
              </Button>
            ) : null}
          </>
        );
      },
    },
  ];

  const handleChange = (index) => {
    // if (index === selectedIndex) return;
    flushSync(() => {
      setSelectedIndex(index);
    });

    query();
  };

  const handleOk = async () => {
    try {
      await formIns.validateFields();
      let { task, time } = formIns.getFieldsValue();
      time = time.format("YYYY-MM-DD HH:mm:ss"); // Form组件提供的方法
      setConfirmLoading(true);
      const { code } = await addTask(task, time);
      if (+code !== 0) {
        message.error("保存失败");
      } else {
        message.success("操作成功");
      }

      setIsModalOpen(false);

      query();
    } catch (_) {}
    setConfirmLoading(false);
  };

  const handleCancel = () => {
    setConfirmLoading(false);
    setIsModalOpen(false);
    formIns.resetFields();
  };

  const handleDel = async (id) => {
    try {
      await removeTask(id);
      query();
    } catch (_) {}
  };

  const completeHandle = async (id) => {
    try {
      await completeTask(id);
      query();
    } catch (_) {}
  };
  const query = async () => {
    setTableLoading(true);
    try {
      let { code, list } = await getTaskList(selectedIndex);
      if (+code !== 0) list = [];
      setTableData(list);
    } catch (_) {}
    setTableLoading(false);
  };

  useEffect(() => {
    query();
  }, [selectedIndex]);

  return (
    <>
      <div className="task-box">
        <div className="header">
          <h2>TASK OA 任务管理系统</h2>
          <Button
            type="primary"
            onClick={() => {
              setIsModalOpen(true);
            }}
          >
            新增任务
          </Button>
        </div>
        <div className="btns">
          {["全部", "未完成", "已完成"].map((item, index) => {
            return (
              <Tag
                key={index}
                color={selectedIndex === index ? "#1677ff" : ""}
                onClick={handleChange.bind(null, index)}
              >
                {item}
              </Tag>
            );
          })}
        </div>
        <div className="table-wrap">
          <Table
            columns={columns}
            rowKey="id"
            dataSource={tableData}
            loading={tableLoading}
            pagination={false}
          />
        </div>
        <Modal
          title="新增任务窗口"
          destroyOnClose
          confirmLoading={confirmLoading}
          open={isModalOpen}
          onOk={handleOk}
          onCancel={handleCancel}
        >
          <Form
            form={formIns}
            layout="vertical"
            autoComplete="off"
            validateTrigger="onBlur"
          >
            <Form.Item
              label="任务描述"
              name="task"
              rules={[
                {
                  required: true,
                  message: "请输入任务描述",
                },
                {
                  min: 6,
                  message: "最少输入6位",
                },
              ]}
            >
              <TextArea autoSize={{ minRows: 2, maxRows: 6 }} />
            </Form.Item>
            <Form.Item
              label="任务预期完成时间"
              name="time"
              rules={[
                {
                  required: true,
                  message: "请输入任务预期完成时间",
                },
              ]}
            >
              <DatePicker className="date-pick" showTime />
            </Form.Item>
          </Form>
        </Modal>
      </div>
    </>
  );
};

export default TaskTestOfFun;
