import { useSelector } from "react-redux";

const VoteMain = function VoteMain(props) {
  const { supNum, oppNum } = useSelector((state) => state.vote );
  return (
    <div className="main">
      <div className="line">支持人数：{supNum} 人</div>
      <div className="line opp">反对人数：{oppNum} 人</div>
    </div>
  );
};

export default VoteMain;
