import { Button } from "antd";
import { useDispatch } from "react-redux";
import { addSup, addOpp } from "../../store/features/voteSlice";

const VoteFooter = function VoteFooter(props) {
  const dispatch = useDispatch();
  return (
    <div className="footer">
      <Button
        type="primary"
        size="small"
        onClick={() => {
          dispatch(addSup());
        }}
      >
        支持
      </Button>
      <Button
        type="primary"
        danger
        size="small"
        onClick={() => {
          dispatch(addOpp());
        }}
      >
        反对
      </Button>
    </div>
  );
};

export default VoteFooter;
